.btn {
    background-color: #e40046;
    color: #ffffff;
    border-radius: 6px;
    padding: 5px 27px;
    margin: 12px;
    border: 2px solid #ffffff;
    transition: .3s;
    font-size: 17px;
}

.btn:hover {
    border: 2px solid #e40046;
    box-shadow: 4px 4px 8px rgb(201, 200, 200);
}

.dispproduct {
    display: grid;
    grid-template-columns: auto auto auto auto;
    justify-content: center;
}

.compcard {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 288px;
    height: 370px;
    margin: 40px 20px;
    border: 1px solid grey;
    border-radius: 8px;
    padding: 25px 10px;
    transform: scale(1);
    transition: .5s;
    box-shadow: 2px 2px 4px gray;
}
.compcard a{
    margin: 20px ;
}

.compcard span{
    font-size: 16px;
    padding: 5px;
}

.compcard:hover {
    transform: scale(1.1);
    box-shadow: 4px 4px 8px gray;
    font-size: 22px;
    font-weight: bold;
}

.wish {
    position: absolute;
    font-size: 28px;
    right: 10%;
    color: rgb(163, 13, 13);
}

@media (max-width: 1800px) {
    .dispproduct {
        display: grid;
        grid-template-columns: auto auto auto auto;
        justify-content: center;
    }

    /* .compcard {
        margin-right: 8%;
    } */
}

@media (max-width: 1280px) {
    .dispproduct {
        display: grid;
        grid-template-columns: auto auto;
    }
}

@media (max-width: 720px) {
    .dispproduct {
        display: grid;
        grid-template-columns: auto;
        justify-content: center;
    }

}